<template>
  <div id="navbar" :class="[{ 'nav-scroll': !scrollNav }]">
    <v-toolbar
      color="primary"
      class="toolbar"
      flat
      :class="[$vuetify.breakpoint.mdAndUp ? 'px-4' : '']"
      :height="$vuetify.breakpoint.mdAndDown ? 72 : 90"
    >
      <section class="logo-section text-center">
        <span id="logo" :class="[logoTheme, logoDisplay]">trypleks</span>
      </section>
      <section
        class="nav-section justify-space-between align-center"
        :class="[$route.path === '/main' ? 'd-flex' : 'd-none']"
      >
        <div class="left-side">
          <div class="mobile d-flex align-center">
            <div class="icon-gender mr-2" v->
              <div
                class="d-flex"
                :class="[
                  iconGender,
                  $vuetify.breakpoint.smAndDown ? 'pa-1' : 'pa-2'
                ]"
                v-if="owner.gender === 'Male'"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  :width="svgWidth"
                  :height="svgHeight"
                  viewBox="0 0 24 26"
                >
                  <path
                    d="M21.292 10.349c-.36-3.908-2.698-6.58-6.2-7.562-.874-.246-2.617-.622-1.092-2.787-4.87 0-10.729 4.235-11.292 10.349-1.859.165-2.708 1.921-2.708 3.964 0 1.755.975 3.967 3.104 4.891 2.935 5.371 7.534 5.796 8.896 5.796s5.961-.425 8.896-5.796c2.129-.925 3.104-3.136 3.104-4.891 0-2.019-.841-3.798-2.708-3.964zm-1.425 7.11c-.246.083-.45.259-.569.491-2.406 4.686-6.194 5.05-7.298 5.05s-4.892-.364-7.298-5.05c-.119-.231-.323-.407-.569-.491-2.448-.829-2.553-4.364-1.553-5.012.235-.152.531-.116.671-.053.661.293 1.406-.191 1.406-.914 0-1.019.151-1.938.43-2.757 1.092-2.56 3.121 1.364 6.913 1.364 3.793 0 5.822-3.925 6.913-1.363.278.819.43 1.738.43 2.756 0 .725.746 1.207 1.406.914.14-.063.436-.1.671.053 1 .648.895 4.183-1.553 5.012zm-3.367-3.959c0 .828-.56 1.5-1.25 1.5s-1.25-.672-1.25-1.5.56-1.5 1.25-1.5 1.25.672 1.25 1.5zm-7.75 1.5c-.69 0-1.25-.672-1.25-1.5s.56-1.5 1.25-1.5 1.25.672 1.25 1.5-.56 1.5-1.25 1.5zm.25 3h6s-.765 2.354-3 2.354c-2.153 0-3-2.354-3-2.354z"
                  />
                </svg>
              </div>
              <div
                class="d-flex pa-1"
                :class="[
                  iconGender,
                  $vuetify.breakpoint.smAndDown ? 'pa-1' : 'pa-2'
                ]"
                v-else
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  :width="svgWidth"
                  :height="svgHeight"
                  viewBox="0 0 24 26"
                >
                  <path
                    d="M16.5 14.492c0 .828-.56 1.5-1.25 1.5s-1.25-.671-1.25-1.5.56-1.5 1.25-1.5 1.25.672 1.25 1.5zm-7.75-1.5c-.69 0-1.25.672-1.25 1.5s.56 1.5 1.25 1.5 1.25-.672 1.25-1.5-.56-1.5-1.25-1.5zm3.25 8.354c2.235 0 3-2.354 3-2.354h-6s.847 2.354 3 2.354zm12-6.041c0 1.765-.985 3.991-3.139 4.906-2.348 3.731-5.484 5.781-8.861 5.781-3.377 0-6.513-2.05-8.862-5.781-2.153-.915-3.138-3.141-3.138-4.906 0-1.472.387-2.937 1.682-3.636-.377-2.311-.117-6.176 4.193-7.593 1.031-1.99 3.125-4.084 6.125-4.084s5.094 2.094 6.125 4.083c4.31 1.418 4.57 5.282 4.193 7.594 1.295.699 1.682 2.164 1.682 3.636zm-2.176-1.252c-4.687-.258-6.833-4.66-7.241-7.394-1.167 6.333-9 1.792-9.896 9.052-.886-.467-1.715-1.308-2.215-2.159-.996.997-.54 4.154 1.661 4.899.224.076.413.228.535.43 1.991 3.296 4.595 5.111 7.332 5.111s5.34-1.815 7.331-5.111c.122-.202.312-.354.535-.43 1.771-.599 2.517-2.769 1.958-4.398z"
                  />
                </svg>
              </div>
            </div>
            <div class="name-group d-flex flex-column">
              <span
                class="body-2 font-weight-regular warning--text"
                v-show="$vuetify.breakpoint.mdAndUp"
                >Hello</span
              >
              <span
                class="font-weight-bold"
                :class="$vuetify.breakpoint.smAndDown ? 'subtitle-1' : 'title'"
              >
                {{ owner.username }}
              </span>
            </div>
          </div>
        </div>
        <div class="right-side">
          <div class="slider-theme">
            <label class="slider">
              <input type="checkbox" @click="$emit('themeApp')" />
              <span class="slider-round"></span>
            </label>
          </div>
        </div>
      </section>
    </v-toolbar>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'Navbar',
  components: {
    // VToolbar: () => import('vuetify/lib')
  },
  data: () => ({
    scrollNav: true,
    scrollPosition: 0
  }),
  computed: {
    logoTheme() {
      return this.$vuetify.theme.dark ? 'logo-dark' : 'logo-light';
    },
    logoDisplay() {
      let breakpoint = val => {
        switch (val) {
          case 'xs':
          case 'sm':
            return 'subtitle-1';
          case 'md':
          case 'lg':
          case 'xl':
            return 'headline';
        }
      };
      return breakpoint(this.$vuetify.breakpoint.name);
    },
    iconGender() {
      return this.$vuetify.theme.dark ? 'dark-gender-icon' : 'gender-icon';
    },
    svgWidth() {
      return this.$vuetify.breakpoint.smAndDown
        ? '17'
        : this.$vuetify.breakpoint.lgAndDown
        ? '23'
        : '30';
    },
    svgHeight() {
      return this.$vuetify.breakpoint.smAndDown
        ? '18'
        : this.$vuetify.breakpoint.lgAndDown
        ? '24'
        : '31';
    },
    ...mapState(['owner'])
  },
  methods: {
    getOwnerData() {
      if (this.$route.name == 'Main') {
        this.getOwner();
      }
    },
    onScroll() {
      const CURRENT_POSITION =
        window.pageYOffset || document.documentElement.scrollTop;
      if (CURRENT_POSITION >= 10) {
        return;
      }
      this.scrollNav = CURRENT_POSITION < this.scrollPosition;
      this.scrollPosition = CURRENT_POSITION;
    },
    scrollNavbar() {},
    ...mapActions(['getOwner'])
  },
  watch: {
    $route: 'getOwnerData'
  },
  async mounted() {
    await this.getOwnerData();
    window.addEventListener('scroll', this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.onScroll);
  }
};
</script>

<style lang="scss" scoped>
#navbar {
  position: fixed;
  transition: all ease-in-out 0.3s;
  left: 0;
  right: 0;
  z-index: 9;
  box-shadow: none;
}
#navbar.nav-scroll {
  box-shadow: 0 2px 15px rgba(71, 120, 120, 0.5);
}
.logo-section {
  position: absolute;
  width: 100%;
  right: 0;
  left: 0;
  #logo {
    font-family: 'Righteous', cursive !important;
  }
  .logo-dark {
    color: #f3f8f3;
  }
  .logo-light {
    color: #f8685f;
  }
}
.nav-section {
  position: relative;
  width: 100%;
}
// slider-btn theme-switch
.slider {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}
.slider input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider-round {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #016080;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 100px;
}
.slider-round:before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}
input:checked + .slider-round {
  background-color: #f8685f;
}
input:focus + .slider-round {
  box-shadow: 0 0 1px #f8685f;
}
input:checked + .slider-round:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
}
// icon
.gender-icon {
  background-color: #016080;
  border-radius: 100%;
  svg {
    fill: #f3f8f3;
  }
}
.dark-gender-icon {
  background-color: #f8685f;
  border-radius: 100%;
  svg {
    fill: #f3f8f3;
  }
}
</style>
